@mixin flex-space-between {
  display: flex;
  justify-content: space-between;
}

@mixin flex-space-between-center {
  @include flex-space-between;
  align-items: center;
}

@mixin line-heigh-margin {
  line-height: 18px;
  margin: 0;
}
@mixin frequent-font-features {
  font-size: 10px;
  font-weight: 400;
}
